/**
 * 
 * @param  {[Function]}  fn     []
 * @param  {[Number]}  delay    []
 * @param  {[Number]}  mustRun  []
 * @return {[Function]}         []
 */
exports.throttle = (fn, wait, mustRun) => {
  let timeout;
  let startTime = new Date();
  return function() {
    let context = this;
    let args = arguments;
    let curTime = new Date();

    clearTimeout(timeout);
    //  handler
    if (curTime - startTime >= mustRun) {
      fn.apply(context, args);
      startTime = curTime;
      // 
    } else {
      timeout = setTimeout(fn, wait);
    }
  };
};



