<template>
    <div class="index_news">
      <div class="find_nav">
        <div class="find_nav_left">
          <div class="find_nav_list">
            <ul id="pagenavi1">
              <li v-for="item in navBars ">
                <span class="navbar-item" style="display: none">{{item.id}}</span>
                <router-link :to="'/news/'+item.id">
                  {{item.name}}
                </router-link>
              </li>
              <li class="sideline"></li>
            </ul>
          </div>
        </div>
      </div>
      <scroller style="top:0.4rem"
                :on-refresh="refresh"
                :on-infinite="infinite"
                ref="my_scroller"
                v-show="!showNoData"
      >
      <div class="news-all" id="new-home">

        <mt-swipe :auto="4000" class="swiper-container" >
          <mt-swipe-item class="slide" v-for="banner in banners" :key="banner.id">
           <router-link :to="'/newsdetail/'+banner.id">
             <img v-lazy="banner.image" alt="" class="swiper-img" @click="goNewsDetail(banner.id)">
             <span class="banner-text">{{banner.title}}</span>
           </router-link>
          </mt-swipe-item>
        </mt-swipe>
        <div class="news-content">
          <ul class="news-item-box" v-show="!showTable">
            <li v-for="item in newDatas" track-by="$index" class="news-item" v-show="item.status===1 || item.status==='1'">
              <router-link  class="news-item-link" :to="'/newsdetail/'+item.id">
                <img v-lazy="item.image" alt="" class="news-icon">
                <div class="news-info-container">
                  <p class="news-title">
                    {{item.title}}
                  </p>
                  <div class="news_time_tag">
                    <span class="news_time">{{item.created_at | filterTime}}</span>
                    <span class="news_tag">{{tagName}}</span>

                  </div>
                  <div class="news_comment_read">
                    <span class="news_comment">{{item.article_comments_count}}</span>
                    <span class="news_read">{{item.article_views_count}}</span>
                  </div>
                </div>
              </router-link>
            </li>
          </ul>
          <table class="table table-hover" v-show="showTable">
            <thead class="my-thread">
            <tr class="my-title-thread">
              <th> / </th>
              <!--<th></th>-->
              <th class="right-item head-item"></th>
              <th class="right-item head-item"></th>
              <th class="die-zhang-info"></th>
            </tr>
            </thead>
            <tbody id="price_list">
            <tr  v-for="item in priceList" @click="goPriceDetailPage(item.id)" class="title-thread">
              <td>
                <span class="xitu-name">{{item.name}}</span>
                <div :class="{'mup':item.flag==='up','mdown':item.flag==='down'}" class="my-t-info m-t-first-info">{{item.min_quote}}{{item.max_quote}}<span v-show="item.flag==='up'"></span><span v-show="item.flag==='down'"></span></div>
              </td>
             <!-- <td :class="{'up':item.flag==='up','down':item.flag==='down'}">{{item.min_quote}}{{item.max_quote}}<span v-show="item.flag==='up'"></span><span v-show="item.flag==='down'"></span></td>-->
              <td :class="{'up':item.flag==='up','down':item.flag==='down' }" class="my-t-info right-item">{{item.avg_quote}}<span v-show="item.flag==='up'" ></span><span v-show="item.flag==='down'"></span></td>
              <td class="my-t-info right-item">{{item.unit}}</td>
              <td class="my-t-info ">
                <span class="my-tip-date">{{item.quote_date}}</span>
                <div class="tie-zhang-box" >
                  <span class="m-di-zhang-tip" :class="{'mxitu-dizhang-up':item.flag==='up','mxitu-dizhang-down':item.flag==='down','mxitu-dizhang-eq':(item.flag!=='up' &&item.flag!=='down')}"><span v-show="item.flag==='up'">+</span>{{item.change}}</span>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
      </scroller>
      <!--<backTop :pos="myCurrentPost" @click="goTop" v-show="!showNoData"></backTop>-->
      <NoData v-show="showNoData"></NoData>
    </div>
</template>
<script>
  import {timeFormat} from '../../utils/times';
  import utils from '../../utils/utils';
  import NoData from '../nodata/NoData.vue';
  import logUtil from '../../utils/log';
  import Api from '../../api/api';
  import {getNewsTab} from '../../api/news'
  import {getNews} from '../../api/news'
  import {getNewsOne} from '../../api/news'
  import {getNewsBanner} from '../../api/news'
  import {getBaoJia} from '../../api/pricelist'
  import {getSignInfo} from '../../api/tiezidetail';
  import timeUtil from '../../utils/timeutil'
  import ftime from '../../utils/ftime'
  import wx from 'weixin-js-sdk';
  export default {
    name: 'app',
    components:{
//      backTop,
      NoData,
    },
    data () {
      return {
        newsId:-1,
        showNoData:false,
//        myCurrentPost:-1,
        navBars:[
          /* '','','',
           '','','',
           '','','',*/
        ],
        tabId:'',
        show: true,
        selected: '1',
        //
        newDatas:[

        ],
        //
        newsTags:[

        ],
        banners:[],
        //
        totalCount:0,
        //
        pageIndex:1,
        //
        pageSize:10,
        type:0,
        swiperOption: {
          pagination: '.swiper-pagination',
          paginationClickable: true,
          spaceBetween: 30,
          centeredSlides: true,
          autoplay: 2500,
          autoplayDisableOnInteraction: false
        },
        tagName:"",
        priceList:[],
        showTable:false,
        mTabWidth:0,
        xituIcon:"http://chuantu.biz/t6/99/1508145943x2890208931.jpg",
        mIndex:0,
      }
    },
    beforeRouteLeave (to, from, next) {
      next();
    },
    filters:{
        filterTime(value){
            return value.split("T")[0];
        }
    },
    mounted(){
      //stop loading more
      this.$refs.my_scroller.finishInfinite(true);
      this.getTabBars();
      const tabId = this.$route.params.newsSID||1;
      if(this.isWeiXin()){
        this.initAsign();
      }else{
        if(parseInt(tabId) === 0){
          this.getBannerData(1);
          this.getPriceListData();
        }else{
          this.showTable = false;
          this.getBannerData(tabId);
          this.getNewsData(0,1,tabId);
        }

      }

      // 
    },
    watch:{
      $route(to){
        //id()
        let reg=/news\/\d+/;
        if(reg.test(to.path)){
          let newsId=this.newsId = this.$route.params.newsSID || 1;
          if(this.isWeiXin()){
            this.initAsign();
          }else{
            if(parseInt(newsId) === 0){
              this.getBannerData(1);
              this.getPriceListData();
            }else{
              this.showTable = false;
              this.getBannerData(newsId);
              this.getNewsData(0,1,newsId);
            }
          }

        }
      },
    },
    methods:{
      /**
       * 
       * */
      isWeiXin(){
        //
        let ua = window.navigator.userAgent.toLowerCase();
        if (ua.match(/MicroMessenger/i) == 'micromessenger') {
          return true;
        } else {
          return false;
        }
      },
      /**
       * 
       * */
      initAsign(){
        const _this = this;
        wx.ready(function () {
          const tabId = _this.$route.params.newsSID||1;
          if(parseInt(tabId) === 0){
            _this.getBannerData(1);
            _this.getPriceListData();
          }else{
            _this.showTable = false;
            _this.getBannerData(tabId);
            _this.getNewsData(0,1,tabId);
          }
        });
      },
      getPriceListData(){
        const that = this;
        const _this = this;
        getBaoJia(timeUtil()).then((res)=>{
          this.priceList = res.data.data.detail;
          const len =this.priceList.length;
          if(len>0){
            this.showTable = true;
            that.showNoData = false;
          }else{
            this.showTable = false;
            that.showNoData = true;
          }
//          var str = window.location.href;
          var str = "http://m.ciyigou.com/mobile/#/news/0";
         /* var regEn1 = /[?]/im;
          if(regEn1.test(str) ) {
            var arr = str.split("?")[0];
            var arr1 = str.split("?")[1].split("#")[1];
            str = arr+"#"+arr1;
          }*/
//          sessionStorage.setItem("width",_this.mTabWidth);
          sessionStorage.setItem("index",_this.mIndex);
          wx.onMenuShareTimeline({
            title: `${ftime()}`, // 
            link: str, // JS
            imgUrl: _this.xituIcon, // 
            success: function (res) {

            },
            cancel: function (res) {

            }
          });
          //
          wx.onMenuShareAppMessage({
            title: `${ftime()}`, // 
            desc: `, `, // 
            link: str, // JS
            imgUrl: _this.xituIcon, // 
            type: '', // ,musicvideolinklink
            dataUrl: '', // typemusicvideo
            success: function (res) {

            },
            cancel: function (res) {

            }
          });
          //QQ
          wx.onMenuShareQQ({
            title: `${ftime()}`, // 
            desc: `, `, // 
            link: str, // 
            imgUrl: _this.xituIcon, // 
            success: function (res) {

            },
            cancel: function (res) {

            }
          });

          //QQ
          wx.onMenuShareQZone({
            title: `${ftime()}`, // 
            desc: `, `, // 
            link: str, // 
            imgUrl: _this.xituIcon, // 
            success: function (res) {

            },
            cancel: function (res) {

            }
          });
        },(err)=>{
          that.showNoData = false;
        });
      },
      goPriceDetailPage(name){
        this.$router.push({path: `/pricedetail/${name}`, replace: true});
      },
        /**
         * banner
         * */
      getBannerData(catId){
          const _this = this;
          getNewsBanner(Api.TOPBANNER,catId).then((res)=>{
            _this.initBanner(res);
          },(err)=>{

          });
      },
      initBanner(res){
        if(res.status === 200 && res.data.data!==null){
            this.banners = res.data.data.detail;
        }
      },
      /**
       * 
       * */
      getTabBars(){
        const that = this;
        let timer = null;
        getNewsTab(Api.CATEGORYS).then((res) => {
          if (res.status === 200 && res.data !== null) {
            that.navBars = res.data.data.detail;
            if(that.navBars.length>0){
              let obj = {};
              obj.id = 0;
              obj.name="";
//              that.navBars.insert(1,obj);
              that.navBars.splice(1, 0, obj);
            }
            timer = setTimeout(function () {
              that.switchTab();
            }, 1000);
          } else {
            clearTimeout(timer);
          }
        }, (err) => {
          clearTimeout(timer);
        });
      },
      switchTab(){
        const _this = this;
        // licssli
        let $window = $(window);
        let initialWindowHeight = null;
        let x1 = 0;
        let x2 = 0;
        let y1 = 0;
        let y2 = 0;
        let ty_left = 0;
        let arrdiv = 0;
        let fnl_x = 0;
        $(".find_nav_list").css("left", 0);

        $(".find_nav_list li").each(function (mIndex) {
          $(".sideline").css({left: 0});
          let index = sessionStorage.getItem("index");
          let width = sessionStorage.getItem("width");
          if(parseInt(index) === mIndex){
            $(".find_nav_list li").eq(mIndex).addClass("find_nav_cur").siblings().removeClass("find_nav_cur");
            $(".sideline").width(width);
            $(".sideline").stop(true);
            $(".sideline").animate({left: $(this).position().left}, 300);
            $(".sideline").animate({width: width});
          }
          //
          console.log("zzzzzzzzzzz:",mIndex,index);
          if(mIndex===0){
            $(".find_nav_list li").eq(0).addClass("find_nav_cur").siblings().removeClass("find_nav_cur");
            let nav_w = $(".find_nav_list li").first().width();
            $(".sideline").width(nav_w);
            $(".sideline").stop(true);
            $(".sideline").animate({left: $(this).position().left}, 300);
            $(".sideline").animate({width: nav_w});
          }

        });
        let nav_w = $(".find_nav_list li").first().width();
        $(".sideline").width(nav_w);
        $(".find_nav_list li").on('click', function () {
          nav_w = $(this).width();
          $(".sideline").stop(true);
          $(".sideline").animate({left: $(this).position().left}, 300);
          $(".sideline").animate({width: nav_w});
          $(this).addClass("find_nav_cur").siblings().removeClass("find_nav_cur");
          let fn_w = ($(".find_nav").width() - nav_w) / 2;
          let fnl_l;
          fnl_x = parseInt($(this).position().left);
          if (fnl_x <= fn_w) {
            fnl_l = 0;
          } else if (fn_w - fnl_x <= flb_w - fl_w) {
            fnl_l = flb_w - fl_w;
          } else {
            fnl_l = fn_w - fnl_x;
          }
          $(".find_nav_list").animate({
            "left": fnl_l
          }, 300);

          _this.mTabWidth = nav_w;
          _this.mIndex = $(this).index();
          console.log("----------:",_this.mTabWidth,_this.mIndex);
          sessionStorage.setItem("width",nav_w);
          sessionStorage.setItem("index",$(this).index());
        });
        let fl_w = $(".find_nav_list").width();
        let flb_w = $(".find_nav_left").width();
        $(".find_nav_list").on('touchstart', function (e) {
          let touch1 = e.originalEvent.targetTouches[0];
          x1 = touch1.pageX;
          y1 = touch1.pageY;
          ty_left = parseInt($(this).css("left"));
        });
        $(".find_nav_list").on('touchmove', function (e) {
          let touch2 = e.originalEvent.targetTouches[0];
          let x2 = touch2.pageX;
          let y2 = touch2.pageY;
          if (ty_left + x2 - x1 >= 0) {
            $(this).css("left", 0);
          } else if (ty_left + x2 - x1 <= flb_w - fl_w) {
            $(this).css("left", flb_w - fl_w);
          } else {
            $(this).css("left", ty_left + x2 - x1);
          }
          if (Math.abs(y2 - y1) > 0) {
            e.preventDefault();
          }
        });
        for (let n = 1; n < 9; n++) {
          let page = 'pagenavi' + n;
          let mslide = 'slider' + n;
          let mtitle = 'emtitle' + n;
          arrdiv = 'arrdiv' + n;
          let as = $("#" + page + "").find("a");
          let tt = new TouchSlider({
            id: mslide,
            'auto': '-1',
            fx: 'ease-out',
            direction: 'left',
            speed: 600,
            timeout: 5000,
            'before': function (index) {
              let as = document.getElementById(this.page).getElementsByTagName('a');
              as[this.p].className = '';
              this.p = index;

              fnl_x = parseInt($(".find_nav_list li").eq(this.p).position().left);

              nav_w = $(".find_nav_list li").eq(this.p).width();
              $(".sideline").stop(true);
              $(".sideline").animate({left: $(".find_nav_list li").eq(this.p).position().left}, 300);
              $(".sideline").animate({width: nav_w}, 100);
              $(".find_nav_list li").eq(this.p).addClass("find_nav_cur").siblings().removeClass("find_nav_cur");
              let fn_w = ($(".find_nav").width() - nav_w) / 2;
              let fnl_l;
              if (fnl_x <= fn_w) {
                fnl_l = 0;
              } else if (fn_w - fnl_x <= flb_w - fl_w) {
                fnl_l = flb_w - fl_w;
              } else {
                fnl_l = fn_w - fnl_x;
              }
              $(".find_nav_list").animate({
                "left": fnl_l
              }, 300);
            }
          });
          tt.page = page;
          tt.p = 0;
          //console.dir(tt); console.dir(tt.__proto__);

          for (let i = 0; i < as.length; i++) {
            (function () {
              let j = i;
              as[j].tt = tt;
              as[j].onclick = function () {
                this.tt.slide(j);
                return false;
              }
            })();
          }
        }
      },
      /**
       *  
       **/
      getNewsData(type,page,newsId){
        const that = this;
//        console.log("newsId:",newsId);
        if(newsId === 0){
          getNewsOne(Api.NEWS,page,that.pageSize).then((res)=>{
            if(res.status === 200){
              that.initNewsData(type,res);
            }
          },(err)=>{
            that.showNoData = true;
            that.$store.dispatch('hideLoading');
            that.showMsg('');
          });
        }else{
          getNews(Api.NEWS,page,newsId,this.pageSize).then((res)=>{
            if(res.status === 200){
              that.initNewsData(type,res);
            }
          },(err)=>{
            that.showNoData = true;
            that.$store.dispatch('hideLoading');
            that.showMsg('');
          });
        }

      },
      /**
       * 
       **/
      initNewsData(type,res){
        logUtil.printLog(":",res);
//        console.log(":",res);
        const that = this;
        that.totalCount = res.data.data.total;
        if(res.data!==null && res.data.data.detail.length>0){
          that.showNoData = false;
          let tempArr = [];
          tempArr = res.data.data.detail;
          if(type===0){
            that.newDatas = tempArr;
          }
          //
          else if(type===1){
            that.newDatas=tempArr.splice(0,that.pageSize);
          }else if(type === 2){ //
            that.newDatas = that.newDatas.concat(tempArr);
          }

          let newsLen = that.newDatas.length;
          for(let i = 0; i<newsLen;i++){
            that.newsTags = that.newDatas[i].tags;
          }
          this.tagName = "";
          let len = this.newsTags.length;
          for(let i=0;i<len;i++){
            if(i<=2){
              if(i+1===len){
                if(i>=2){
                  this.tagName+=this.newsTags[i].name+"......";
                }else{
                  this.tagName+=this.newsTags[i].name;
                }

              }else {
                this.tagName+=this.newsTags[i].name+"";
              }
            }
          }
        }else{
          that.showNoData = true;
          that.showMsg('!');
        }
      },
      /**
       * 
       * */
      showMsg(str){
        this.$toast({
          message: str,
          duration: 2000,
        });
      },
      /**
       * 
       * */
      refresh(done) {
        const that = this;
        that.pageIndex = 1;
        that.type = 1;
//        console.log("refresh that.newsId:",that.$route.params.newsSID);
        setTimeout(() => {
          if(parseInt(that.$route.params.newsSID) === 0){
            that.getBannerData(1);
            that.getPriceListData();
          }else{
            that.showTable = false;
            that.getNewsData(that.type,that.pageIndex,that.$route.params.newsSID);
          }

          done();
        }, 1500)
      },
      /**
       * 
       * */
      infinite(done) {
        const that = this;
        that.pageIndex++;
        that.type = 2;
//        console.log("loadMore that.newsId:",that.$route.params.newsSID);
        setTimeout(() => {
          //
          if(that.pageIndex>=that.totalCount){
            done(true);
            return;
          }
          done();
          if(parseInt(that.$route.params.newsSID) === 0){
            that.getBannerData(1);
            that.getPriceListData();
          }else{
            that.showTable = false;
            that.getNewsData(that.type,that.pageIndex,that.$route.params.newsSID);
          }

        }, 1500)
      },
    },
  }
</script>
